.download {
  background-color: #1d2631;
  min-height: 271px;
}

.download__wrapper {
  width: 320px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 28px 20px 30px 20px;
  box-sizing: border-box;
  margin: 0 auto;

  @include tablet {
    margin-left: 0;
  }

  @include desktop {
    margin-left: 0;
  }
}

@media (min-width: 660px) {
  .download {
    background-color: transparent;
    position: relative;
    padding: 0;
    padding-top: 2px;
    width: 660px;
    margin: 0 auto;
  }
}

@media (min-width: 960px) {
  .download {
    padding-top: 7px;
    width: 960px;
  }
}

.download__button {
  @include button;
  color: #ffffff;
  border: 3px solid #ffffff;
}

.download__button:hover {
  color: #d22856;
  background-color: #ffffff;

  @include hover;
}

.download__button:active {
  color: rgba(#d22856, 0.3);
}

@media (min-width: 660px) {
  .download__button {
    padding: 15px 45px;
    margin-bottom: 44px;
    margin-left: 20px;
  }
}

@media (min-width: 960px) {
  .download__button {
    margin-bottom: 68px;
    margin-left: 0;
    padding: 15px 30px;
    font-size: 21px;
  }
}

.download__list {
  list-style: none;
  margin: 0;
  padding: 0;
  margin-top: 30px;
  display: flex;
  align-items: baseline;
  min-width: 200px;
  justify-content: space-between;
}

@media (min-width: 660px) {
  .download__list {
    margin: 0;
    margin-left: 15px;
  }
}

@media (min-width: 960px) {
  .download__list {
    margin-bottom: 20px;
    margin-left: 0;
  }
}

.download__description {
  color: #ffffff;
  font-weight: 300;
  font-size: 14px;
  line-height: 24px;
  text-align: center;
  margin: 0;
  margin-top: 18px;
}

@media (min-width: 660px) {
  .download__description {
    font-size: 18px;
    min-width: 300px;
    margin-top: 31px;
    margin-left: 20px;
  }
}

@media (min-width: 960px) {
  .download__description {
    margin-left: 0;
  }
}
